import { useTranslation } from "react-i18next";
import Modal from "../../../Ui/Modal/Modal";
import style from "../../../UserStaffMember/PlateformUser/PlateformUser.module.css";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import StaffTimetableSubjectListCard from "./StaffTimetableSubjectListCard";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { useStaffTimetableSubjectListStats } from "../../../Hooks/ApiHooks/StaffManager/staff-manager-api";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
const StaffTimetableSubjectList = ({ label, onClose, clsId, category }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const {
    staffTimetableSubjectListStats,
    staffTimetableSubjectListStatsLoading,
  } = useStaffTimetableSubjectListStats({
    data: {
      cid: clsId,
      page: page,
      limit: 10,
      search: search,
      flow: category,
    },
    skip: clsId ? !category : !clsId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (staffTimetableSubjectListStats?.list?.length) {
      let obj = nextPage(staffTimetableSubjectListStats?.list?.length);
      setState(() => obj);
    }
  }, [staffTimetableSubjectListStats?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) {
      if (!staffTimetableSubjectListStatsLoading) onDebounce(e.target.value);
    } else {
      setPage(() => 1);
      setSearch("");
    }
  };
  return (
    <Modal
      onClose={onClose}
      customOverlayStyle={{
        width: "70%",
      }}
    >
      <div
        className={style.modal_container}
        style={{
          maxWidth: "100%",
          maxHeight: "calc(100dvh - 100px)",
        }}
      >
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t(label)}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.3rem",
            paddingBottom: "1rem",
          }}
        >
          <div className={style.assign_search_container}>
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              style={{
                width: "100%",
              }}
              onChange={onSearchEvent}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
              style={{
                left: "1%",
              }}
            />
          </div>
          {staffTimetableSubjectListStats?.list?.map((subject, index) =>
            staffTimetableSubjectListStats?.list?.length === index + 1 ? (
              <div key={subject?._id} ref={ref}>
                <StaffTimetableSubjectListCard subject={subject} />
              </div>
            ) : (
              <StaffTimetableSubjectListCard
                subject={subject}
                key={subject?._id}
              />
            )
          )}
          {staffTimetableSubjectListStatsLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            staffTimetableSubjectListStats?.list?.length === 0 && (
              <EmptyMessage
                title="No subject found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            staffTimetableSubjectListStats?.list?.length === 0 && (
              <EmptyMessage
                title="No subject."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default StaffTimetableSubjectList;
