import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import style from "../InstituteAnalytic.module.css";
import { useState } from "react";
import StaffTimetableSubjectList from "./StaffTimetableSubjectList";

const StaffTimetableCard = ({ cls }) => {
  const [step, setStep] = useState("");

  const onClose = () => {
    setStep("");
  };
  return (
    <>
      <div
        className={style.staclc_card}
        style={{
          justifyContent: "space-between",
        }}
      >
        <div className={style.staclc_card_container}>
          <img
            src={
              cls?.classTeacher?.staffProfilePhoto
                ? `${imageShowUrl}/${cls?.classTeacher?.staffProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
          />
          <div className={style.staclc_card_inner}>
            <h6>
              {cls?.className ?? ""} - {cls?.classTitle ?? ""}
            </h6>
            <p>
              {`${cls?.classTeacher?.staffFirstName ?? ""} ${
                cls?.classTeacher?.staffMiddleName ?? ""
              } ${cls?.classTeacher?.staffLastName ?? ""}`}
            </p>
          </div>
        </div>
        <div className={style.stc_stats}>
          <span
            onClick={() =>
              cls?.updated_timetable_count > 0 ? setStep(1) : null
            }
          >
            {cls?.updated_timetable_count ?? 0}
          </span>{" "}
          /{" "}
          <span
            onClick={() =>
              cls?.not_updated_timetable_count > 0 ? setStep(2) : null
            }
          >
            {cls?.not_updated_timetable_count ?? 0}
          </span>
        </div>
      </div>
      {step === 1 ? (
        <StaffTimetableSubjectList
          label="timetable_updated_subject"
          onClose={onClose}
          clsId={cls?._id}
          category="Yes"
        />
      ) : step === 2 ? (
        <StaffTimetableSubjectList
          label="timetable_not_updated_subject"
          onClose={onClose}
          clsId={cls?._id}
          category="No"
        />
      ) : null}
    </>
  );
};

export default StaffTimetableCard;
